<template>
  <BaseLayout title="企业账户" :subtitle="subtitle" bg="linear-gradient(180deg, #EFF, #F9F9F9 10%, #F9F9F9)" @click-title="onTitle" noback>
    <template #extra>
      <van-button icon="edit" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" v-show="authorized" @click="onTitle">账户设置</van-button>
    </template>

    <div class="content">

      <van-cell title="员工" class="card" center is-link to="/team/member">
        <template #icon><van-icon class="icon" name="friends" color="#69F" size="32"/></template>
        <span class="value">0 个团队<br>{{state.summary.member}} 名员工<br></span>
      </van-cell>

      <van-cell title="账单" class="card" center is-link to="/team/property">
        <template #icon><van-icon class="icon" name="balance-list" color="#F9F" size="32"/></template>
        <span class="value">累计充值 {{state.summary.cash}} 元<br>剩余积分 {{state.summary.point}} 分</span>
      </van-cell>

      <van-cell title="发票" class="card" center is-link to="/team/invoice">
        <template #icon><van-icon class="icon" name="coupon" color="#6CC" size="32"/></template>
        <span class="value">已开发票 {{state.summary.invoice}} 元<br>待开发票 {{state.summary.cash - state.summary.invoice}} 元</span>
      </van-cell>

      <van-cell title="礼品卡" class="card" center is-link to="/team/card">
        <template #icon><van-icon class="icon" name="gift-card" color="#CC3" size="32"/></template>
        <span class="value">共 {{state.summary.card.total}} 张<br>合计 {{state.summary.card.amount}} 元</span>
      </van-cell>


    </div>
  </BaseLayout>
</template>

<style scoped>

.content {
  margin: 10px;
}

.toolbar {
  margin: 20px 0px;
  font-size: 14px;
}

.card {
  height: 90px;
  margin-top: 5px;
  background: #FFF;
  border-radius: 5px;
  color: #666;
  font-size: 18px;
  font-weight: 600;
}
.icon {
  margin-right: 10px;
}
.value {
  font-size: 12px;
  font-weight: 400;
}

.menu {
  margin: 20px 0;
}
.item {
  color: #666;
}
</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  data() {
    const state = reactive({
      summary: {
        info: {},
        cash: 0,
        point: 0,
        member: 0,
        card: 0,
        invoice: 0,
      },
    });
    return {state};
  },

  created() {
    this.$http.post("/team/summary").then((data) => {
      this.state.summary = data;
    });
  },

  computed: {
    subtitle(){
      return _.isEmpty(this.state.summary.info) ? "正在查询 ...": this.state.summary.info.name;
    },
    
    ...mapState(["profile"]),
    ...mapGetters(["authorized", "uid", "nickname", "cash", "point", "level"]),
  },

  methods: {
    onTitle() {
      this.$router.push(this.authorized ? "/my/profile" : "/login");
    },
    ...mapMutations(["auth"]),
  }
}
</script>
